.navbar {
  padding: 0.5rem 2rem;
  background: lightgrey;
}

.navbar ul {
  display: flex;
  list-style-type: none;
  align-items: center;
}

.navbar ul li {
  padding-right: 1rem;
  color: black;
  cursor: default;
}

.navbar ul .link {
  text-decoration: none;
}

.navbar ul .nav-cart {
  position: relative;
  margin-left: auto;
}

.navbar ul .fa-shopping-cart {
  font-size: 2rem;
}

.navbar ul .cart-badge {
  position: absolute;
  color: white;
  top: 0.1rem;
  right: 1.45rem;
}

.store-dropdown {
  position: absolute;
  width: 100%;
  height: 20%;
  justify-content: space-around;
  align-items: stretch;
  z-index: 1;
}

.store-dropdown ul {
  display: flex;
  list-style-type: none;
  height: 100%;
}

.store-dropdown ul .link-earbud {
  flex: 1 1;
  background: url(/online-store/static/media/photo-1570993492903-ba4c3088f100.6de97cd5.png);
  background-position: center center;
}

.store-dropdown ul .link-headphone {
  flex: 1 1;
  background: url(/online-store/static/media/headphones-3683983__340.f30870cf.jpg);
  background-position: center center;
}

.store-dropdown ul .link-speaker {
  flex: 1 1;
  background: url(/online-store/static/media/photo-1580398813894-ec4789b84bf6.f6b0f2a6.png);
  background-position: center center;
}

.store-dropdown ul .link {
  display: flex;
  justify-content: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.store-dropdown ul .link li {
  -ms-grid-row-align: center;
  align-self: center;
  color: white;
}

.product-window {
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: (minmax(300px, 1fr))[auto-fill];
      grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  grid-auto-rows: 350px;
  grid-gap: 2rem;
  gap: 2rem;
  margin: 2rem;
}

@media (max-width: 600px) {
  .product-window {
    -ms-grid-columns: (minmax(200px, 1fr))[auto-fill];
        grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  }
}

.filter-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 1rem;
  padding: 0.5rem 1rem;
  border-bottom: 1px solid black;
}

@media (max-width: 600px) {
  .filter-bar h1 {
    font-size: 1em;
  }
}

.filter-window {
  padding: 1rem;
  margin: 0 1rem;
  overflow: auto;
  display: flex;
  justify-content: space-around;
  border-bottom: 1px solid black;
}

.filter-window h2 {
  font-size: 1em;
  letter-spacing: 0.2em;
}

.filter-window .filter-seperator {
  -ms-grid-column-align: stretch;
      justify-self: stretch;
  padding: 1px;
  background: lightblue;
}

@media (max-width: 600px) {
  .filter-window .open-filter {
    display: flex;
    justify-content: space-between;
    padding: 1rem;
    background: lightgrey;
    margin: 1rem 0;
  }
}

@media (max-width: 600px) {
  .filter-window {
    display: block;
  }
}

.filter-search {
  margin: 1rem 0;
}

.filter-search input {
  width: 100%;
  padding: 1rem;
  height: 2rem;
  border-radius: 0.5rem;
  border: 1px solid black;
}

.filter-inputs {
  display: flex;
  align-items: center;
  margin: 1rem;
}

.filter-inputs input[type="checkbox"] {
  display: none;
}

.filter-inputs label {
  display: inline-block;
  position: relative;
  vertical-align: middle;
  margin: 5px;
  cursor: pointer;
}

.filter-inputs input[type="checkbox"] + label:before {
  content: "";
  border: 2px solid #ddd;
  display: inline-block;
  vertical-align: middle;
  width: 10px;
  height: 10px;
  padding: 2px;
  margin-right: 10px;
  text-align: center;
}

.filter-inputs input[type="checkbox"]:checked + label:after {
  content: "";
  padding: 2px;
  text-align: center;
  position: absolute;
  width: 1px;
  height: 5px;
  border: solid blue;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  top: 2px;
  left: 5px;
}

.filter-tags {
  display: flex;
  margin: 0 1rem;
  padding: 0.5rem;
  border-bottom: 1px solid black;
}

.filter-tags .tags {
  display: flex;
  flex-wrap: wrap;
}

.filter-tags button {
  background: lightgrey;
  border: none;
  padding: 5px 10px;
  margin: 5px;
  align-self: flex-start;
  margin-left: auto;
}

.filter-tags button:hover {
  background: silver;
  text-decoration: underline;
}

.empty-filter-tag {
  padding: 1.25rem 0;
}

.tag {
  display: flex;
  align-items: center;
  margin: 5px;
  padding: 2px 5px;
  background: lightblue;
}

.tag p {
  margin: 0 7px;
}

.tag:hover {
  background: dodgerblue;
}

.product-card {
  text-align: center;
  background: gainsboro;
}

.image-link {
  display: inline-block;
  overflow: hidden;
}

.product-card-image {
  object-fit: cover;
  width: 100%;
  height: 250px;
  -webkit-transition: -webkit-transform 0.5s;
  transition: -webkit-transform 0.5s;
  transition: transform 0.5s;
  transition: transform 0.5s, -webkit-transform 0.5s;
}

.product-card-image:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

.color-picker {
  display: inline-block;
  width: 20px;
  height: 20px;
  margin: 5px;
  border-radius: 1rem;
}

.color-picker:hover {
  opacity: 0.8;
}

.link {
  text-decoration: none;
  color: black;
}

.link:hover {
  text-decoration: underline;
}

.product-bar {
  border-bottom: 1px solid black;
  margin: 0 1rem 1rem 1rem;
}

.product-bar button {
  border: none;
  background: none;
  padding: 1rem;
}

.product-bar button:hover {
  text-decoration: underline;
}

.product-details {
  display: flex;
}

@media (max-width: 600px) {
  .product-details {
    flex-direction: column;
  }
}

.product-banner {
  width: 50%;
  margin: 2rem;
}

.product-banner .banner-image {
  position: relative;
}

.product-banner .banner-image img {
  display: block;
  object-fit: cover;
  width: 100%;
  height: 400px;
}

.product-banner .banner-image .banner-arrow {
  position: absolute;
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: 100%;
  top: 0;
  font-size: 2em;
}

.product-banner .banner-image .banner-arrow .banner-arrow-left {
  display: flex;
  align-items: center;
  padding: 2rem;
  color: lightgrey;
}

.product-banner .banner-image .banner-arrow .banner-arrow-left:hover {
  background: lightgrey;
  opacity: 0.5;
  color: black;
}

.product-banner .banner-image .banner-arrow .banner-arrow-right {
  display: flex;
  align-items: center;
  padding: 2rem;
  color: lightgrey;
}

.product-banner .banner-image .banner-arrow .banner-arrow-right:hover {
  background: lightgrey;
  opacity: 0.5;
  color: black;
}

@media (max-width: 600px) {
  .product-banner {
    width: 100%;
    margin: 0;
  }
  .product-banner .banner-image {
    margin: 2rem;
  }
}

.banner-image-container {
  flex: 1 1;
  display: flex;
  justify-content: space-around;
  margin: 2rem;
}

.banner-image-container .banner-image-selector {
  width: 3rem;
  height: 3rem;
  cursor: pointer;
}

.product-info {
  flex: 1 1;
  margin: 0 2rem;
}

.product-info h1 {
  padding: 1rem 0;
  border-bottom: 1px solid lightgrey;
}

.product-info h2 {
  margin: 1rem 0;
  font-size: 1.5em;
}

.product-info h3 {
  margin: 1rem 0;
  font-size: 1em;
}

.product-info .product-features {
  margin: 1rem 0;
}

.product-info .product-features li {
  list-style-position: inside;
  margin: 1rem 0;
}

.product-info .product-description {
  margin: 1rem 0;
}

.color-selector p {
  margin: 1rem 0;
}

.color-selector span {
  content: "";
  display: inline-block;
  width: 1.5rem;
  height: 1.5rem;
  margin-right: 1rem;
  border: 2px solid grey;
}

.color-selector span:hover {
  border: 2px solid black;
}

.product-qty {
  margin: 2rem 0;
}

.product-add {
  display: flex;
  margin: 1rem 0;
}

.product-add .cart-add-button {
  width: 50%;
  background: lightblue;
  border: none;
}

.product-add .cart-add-button:hover {
  background: dodgerblue;
}

.product-add .product-count {
  display: flex;
  margin-right: 5rem;
}

.product-add .product-count button {
  padding: 1rem 1.5rem;
  background: lightgrey;
  border: none;
}

.product-add .product-count button:hover {
  background: silver;
}

.product-add .product-count input {
  width: 5rem;
  text-align: center;
  border: 1px solid lightgrey;
}

.cart {
  display: flex;
  flex-direction: column;
  position: absolute;
  right: 0.5rem;
  margin: 1rem;
  max-height: 80%;
  background: lightgray;
  border-radius: 1rem;
  padding: 2rem;
  z-index: 1;
}

.cart .cart-list {
  flex: 3 1;
  max-height: 80%;
  overflow: auto;
}

.cart .cart-list::-webkit-scrollbar {
  width: 5px;
}

.cart .cart-list::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.1);
}

.cart .cart-list::-webkit-scrollbar-thumb {
  background-color: darkgrey;
  outline: 1px solid slategrey;
}

.cart .cart-receipt {
  flex: 1 1;
}

@media (max-width: 600px) {
  .cart {
    right: 0;
    margin: 0;
    border-radius: 0;
    width: 100%;
    max-height: 60%;
  }
}

.cart-item {
  display: flex;
  margin-bottom: 2rem;
}

.cart-item .cart-item-img {
  display: inline-block;
}

.cart-item .cart-item-img img {
  object-fit: cover;
  width: 150px;
  height: 150px;
}

.cart-item .cart-item-details {
  margin: 0 1rem;
  width: 60%;
}

.cart-item .cart-item-title {
  word-wrap: break-word;
  font-weight: bold;
}

.cart-item .cart-item-price {
  font-weight: bold;
}

.cart-item .cart-item-qty {
  display: flex;
  margin: 0.5rem 0;
}

.cart-item .cart-item-qty input {
  width: 3rem;
  text-align: center;
  border: none;
}

.cart-item .cart-item-qty button {
  border: none;
  width: 2rem;
  height: 2rem;
}

.cart-item .cart-item-qty button:hover {
  background: silver;
}

.cart-item .cart-item-delete {
  margin: 0.5rem 0;
  padding: 0.5rem;
  border: none;
  background: silver;
}

.cart-item .cart-item-delete:hover {
  background: grey;
}

.cart-receipt {
  flex: 1 1;
  display: flex;
  flex-direction: column;
  margin-top: 1rem;
  border-top: 1px solid gray;
}

.cart-receipt p {
  align-self: flex-end;
  margin: 1rem 0;
}

.cart-receipt button {
  -ms-grid-row-align: center;
  align-self: center;
  padding: 0.5rem 0;
  width: 100%;
  background: dodgerblue;
  border: none;
  border-radius: 1rem;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  font-family: "Metrophobic", sans-serif;
  height: 100%;
}

#root {
  height: 100%;
}

.app {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.app .app-bar {
  flex: 0 1 auto;
}

.app .app-main {
  flex: 1 1 auto;
}

body::-webkit-scrollbar {
  width: 5px;
}

body::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}

body::-webkit-scrollbar-thumb {
  background-color: darkgrey;
  outline: 1px solid slategrey;
}

